import { put, fork, take,takeLatest } from 'redux-saga/effects';
import Axios from 'axios';
import * as homeAction from '../action/homeAction';
import * as types from '../action/types';
import { ndhpCdn } from '../host';
const apiGetHome = `${englishCdn}/homePageData.json`;

// put = vs dispatch
function* getHomeSaga() {
  while (true) {
    yield take(types.GET_HOME);
    try {
      const res = yield Axios.get(apiGetHome);
      const { data } = res;
      yield put(homeAction.getHomeSuccess(data));
    } catch (err) {
      console.log(err);
      yield put(homeAction.getHomeErr(err));
    }
  }
}

function* homeSaga() {
  yield fork(getHomeSaga);
}
export default homeSaga;


